import React, { useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import './Navbar.css';

const Navbar = ({ toggleTheme, currentTheme }) => {
  const [menuOpen, setMenuOpen] = useState(false);
  const location = useLocation();

  const toggleMenu = () => {
    setMenuOpen(!menuOpen);
  };
  

  return (
    <nav className="navbar">
      <div className="navbar-brand">EMS Dashboard</div>
      <button className="navbar-toggle" onClick={toggleMenu}>
        ☰
      </button>
      <ul className={`navbar-links ${menuOpen ? 'show' : ''}`}>
        <li>
          <Link to="/" className={location.pathname === '/' ? 'active' : ''} onClick={() => setMenuOpen(false)}>Home</Link>
        </li>
        <li>
          <Link to="/register" className={location.pathname === '/register' ? 'active' : ''} onClick={() => setMenuOpen(false)}>Add Employee</Link>
        </li>
        <li>
          <Link to="/employees" className={location.pathname === '/employees' ? 'active' : ''} onClick={() => setMenuOpen(false)}>Employee Details</Link>
        </li>
        <li>
          <Link to="/leaves" className={location.pathname === '/leaves' ? 'active' : ''} onClick={() => setMenuOpen(false)}>Leaves</Link>
        </li>
        <li>
          <Link to="/settings" className={location.pathname === '/settings' ? 'active' : ''} onClick={() => setMenuOpen(false)}>Settings</Link>
        </li>
        <li>
          <Link to="/help" className={location.pathname === '/help' ? 'active' : ''} onClick={() => setMenuOpen(false)}>Help</Link>
        </li>
      </ul>
  
    </nav>
  );
};

export default Navbar;
