import React, { useState, useContext, useEffect } from 'react';
import { GlobalContext } from '../Context/GlobalState';
import { v4 as uuidv4 } from 'uuid';
import './RegisterEmployee.css';

const RegisterEmployee = () => {
  const { addEmployee } = useContext(GlobalContext);

 
  const [employee, setEmployee] = useState({
    name: '',
    age: '',
    email: '',
    contact: '',
    position: '', 
  });

  
  const [employeeId, setEmployeeId] = useState('');

  
  const [showModal, setShowModal] = useState(false);

  
  useEffect(() => {
    const generatedId = uuidv4();
    setEmployeeId(generatedId); 
  }, []);

  
  const handleChange = (e) => {
    setEmployee({ ...employee, [e.target.name]: e.target.value });
  };

  
  const handleSubmit = (e) => {
    e.preventDefault();

    
    const newEmployee = {
      id: employeeId,  
      ...employee,
    };

    addEmployee(newEmployee); 

    
    const newEmployeeId = uuidv4();
    setEmployeeId(newEmployeeId); 

    
    setEmployee({ name: '', age: '', email: '', contact: '', position: '' });

    
    setShowModal(true);
  };

  
  const closeModal = () => {
    setShowModal(false);
  };

  return (
    <div className="register-employee">
      <h2>Add Employee</h2>

      
      <div className="form-group">
        <label>Employee ID:</label>
        <p>{employeeId}</p> 
      </div>

      <form onSubmit={handleSubmit}>
        
        <div className="form-group">
          <label>Name:</label>
          <input
            type="text"
            name="name"
            value={employee.name}
            onChange={handleChange}
            required
            placeholder='e.g., John Dune'
          />
        </div>

       
        <div className="form-group">
          <label>Age:</label>
          <input
            type="number"
            name="age"
            value={employee.age}
            onChange={handleChange}
            required
            min="18"
            max="65"
          />
        </div>

       
        <div className="form-group">
          <label>Email:</label>
          <input
            type="email"
            name="email"
            value={employee.email}
            onChange={handleChange}
            required
          />
        </div>

        
        <div className="form-group">
          <label>Contact:</label>
          <input
            type="tel"
            name="contact"
            value={employee.contact}
            onChange={handleChange}
            required
            pattern="[0-9]{10}"
            title="Enter a valid 10-digit phone number"
          />
        </div>

        
        <div className="form-group">
          <label>Position:</label>
          <input
            type="text"
            name="position"
            value={employee.position}
            onChange={handleChange}
            required
            placeholder="e.g., Developer, Manager"
          />
        </div>

       
        <button type="submit" disabled={!employee.name || !employee.age || !employee.email || !employee.contact || !employee.position}>
          Submit
        </button>
      </form>

     
      {showModal && (
        <div className="modal">
          <div className="modal-content">
            <h3>Employee Details Add Successfully!!!</h3>
            <button onClick={closeModal}>Close</button>
          </div>
        </div>
      )}
    </div>
  );
};

export default RegisterEmployee;
